import React, { useCallback } from 'react';
import styled from 'styled-components';
import { vw } from '@/styles/utils';
import { useHistory } from 'react-router-dom';
import useResetHook from '@/plugins/hooks/useResetHook';
import { useRef } from 'react';
import contentMatrix from '@/data/contentMatrix';
import DivWithRefControl from '@/plugins/components/DivWithRefControl';

export default function StartOverButton({
  tabIndex,
  zindex,
  handleArrowBoxBlur,
  startOverRef,
  nextItemRef,
  sideTabRef,
}) {
  const reset = useResetHook();
  
  return (
    <Root  
      onClick={reset}
      handleKeyDown={reset}
      onFocus={handleArrowBoxBlur}
      tabIndex={tabIndex}
      zindex={zindex}
      aria-roledescription={`button, ${contentMatrix.main.pressAD}`}
      ref={startOverRef}
      prevTabRef={sideTabRef}
      nextTabRef={nextItemRef}
    >
      <span>Start Over</span>
    </Root>
  );
}

const Root = styled(DivWithRefControl)`
  z-index: ${props=>props.zindex===null ? '' : props.zindex};
  position: absolute;
  width: ${vw(180)};
  height: ${vw(52)};
  left: ${vw(35)};
  bottom: ${vw(30)};
  padding-top: ${vw(5)};
  font-size: ${vw(22)};
  font-family: ${({ theme }) => theme.font.tradegothic};
  font-weight: 600;
  font-style: normal;
  color: ${props=>props.zindex==='999' ? '#000' : ({ theme }) => theme.color.tertiary};
  background-image: url(${require('@/assets/images/icons/startover.svg')});
  background-repeat: no-repeat;
  background-size: ${vw(50)} ${vw(52)};
  span {
    margin-left: ${vw(30)};
    /* background: green; */
  }
`;
