import contentMatrix from '@/data/contentMatrix';

const views = contentMatrix.main.views;
// Constants
export const VIEWS__SET = 'VIEWS::SET';

// Actions
export const setActiveView = activeViewIndex => {
  return {
    type: VIEWS__SET,
    payload: { activeViewIndex, activeView: views[activeViewIndex] },
  };
};

// Initial State
const initialState = {
  activeViewIndex: 0,
  activeView: views[0],
};

// Reducer
export default (state = initialState, action = {}) => {
  const { payload } = action;
  switch (action.type) {
    case VIEWS__SET:
      return {
        ...state,
        activeViewIndex: payload.activeViewIndex,
        activeView: payload.activeView,
      };
    default:
      return state;
  }
};
