const getContentMatrixAssets = () => {
  const views = contentMatrix.main.views;
  const images = _.reduce(
    views,
    (result, view) => {
      const newImages = [];
      newImages.push(view.thumb);
      newImages.push(view.panorama.image);
      return _.concat(result, newImages);
    },
    []
  );
  const hotspotImages = _.reduce(
    views,
    (result, view) => {
      const imgs = _.map(view.hotspots, (hotspot) => {
        return hotspot.src;
      });
      return _.concat(result, imgs);
    },
    []
  );
  return _.concat(images, hotspotImages);
};

