import React, {useState, useEffect, useRef} from 'react';
import styled from 'styled-components';
import PropTypes from 'prop-types';
import { vw } from '@/styles/utils';
import contentMatrix from '@/data/contentMatrix';
import KeypadVerticalNavList from '@/plugins/components/KeypadVerticalNavList';
import DivWithRefControl from '@/plugins/components/DivWithRefControl';
import { loader } from "@/plugins/components/Preloader";
import { HOTSPOTTAB_ARROW_LEFT, HOTSPOTTAB_ARROW_RIGHT, HOTSPOTTAB_CLOSE_BUTTON, HOTSPOTTAB_OPEN_BUTTON, HOTSPOTTAB_BG } from '@/config/config';

export default function HotspotTab({
  active,
  isButtonActive,
  hotspot,
  handleClickTabButton,
  activeView,
  activeHotspotIndex,
  handleFocus,
  groupRef,
  startOverRef,
  onClickNext,
  onClickPrevious,
  tabIndex,
}) {
  const closePanelButtonRef = useRef();
  const handleClickClose = () => {
    handleClickTabButton();
  };

  const handleClickNext = (index) => {
    onClickNext(index);
  };
  const handleClickPrevious = (index) => {
    onClickPrevious(index);
  };

  const handleKeyDown = (e)=>{
    if(e.key==="Enter"){
      handleClickClose();
    }
  }

  return (
    <Root active={active } >

      {active &&  (
       
        <Content 
          active={activeHotspotIndex!==null}
          nextTabRef={closePanelButtonRef}
          prevTabRef={startOverRef}
          tabIndex={tabIndex}
          onFocus={handleFocus}
          ref={groupRef}
          aria-label={contentMatrix.main.tabGroup.infoPanelGroupAD}
          aria-roledescription={' '} 
        >
          <Header
            aria-roledescription={' '}
          ><h1>{hotspot.title}</h1></Header>
          <Body
            aria-roledescription={' '}
            width={hotspot}
          ><p>{hotspot.body}</p></Body>
          <Image 
            src={loader.get(hotspot.src).src}  
            alt={hotspot.img_alt}  
            tabIndex={-1}
            active={active}
            aria-roledescription={' '}
            
          />

          <ButtonSection
            aria-roledescription={' '}
            aria-label={`Feature ${activeHotspotIndex + 1} of ${activeView.hotspots.length}, selected`}
          >
        <h3
        >
          {activeHotspotIndex + 1} of {activeView.hotspots.length}
        </h3>
      </ButtonSection>
      <NextPrevButton
          src={loader.get(HOTSPOTTAB_ARROW_RIGHT).src}
          left={215}
          onClick={() => handleClickNext(activeHotspotIndex)}
          tabIndex={-1}
          aria-roledescription={contentMatrix.main.tabArrowRightAD}
        ></NextPrevButton>

        <NextPrevButton
          src={loader.get(HOTSPOTTAB_ARROW_LEFT).src}
          left={0}
          onClick={() => handleClickPrevious(activeHotspotIndex)}
          tabIndex={-1}
          aria-roledescription={contentMatrix.main.tabArrowLeftAD}
        ></NextPrevButton>

        </Content>
      )}
      <CLoseOpenButton 
        onClick={handleClickClose}
        handleKeyDown={handleClickClose} 
        active={active}
        show={isButtonActive}
        tabIndex={isButtonActive ? 2 : -1}
        ref={closePanelButtonRef}
        prevTabRef={groupRef}
        aria-label={active 
          ? contentMatrix.main.tabGroup.closeButtonAD  
          : contentMatrix.main.tabGroup.openButtonAD
        }
        aria-roledescription={' '}
        
      >
        <Arrow
           src={
            active
              ? loader.get(HOTSPOTTAB_CLOSE_BUTTON).src
              : loader.get(HOTSPOTTAB_OPEN_BUTTON).src
          }
          active={active}
        
        />
      </CLoseOpenButton>

    
     
    </Root>
  );
}

HotspotTab.propTypes = {
  active: PropTypes.bool,
  hotspot: PropTypes.object,
  onClickHotspot: PropTypes.func,
};
HotspotTab.defaultProps = {
  active: false,
  hotspot: {},
  onClickHotspot: null,
};

const Root = styled.div`
  position: absolute;
  top: 0;
  left: 0;
  width: ${vw(635)};
  height: ${vw(1090)};

  background-image: url(${loader.get(HOTSPOTTAB_BG).src});
  background-size: cover;
  z-index: 999;
  transform: ${(props) =>
    props.active ? `translateX(0)` : 'translateX(-100%)'};
  transition: transform 0.5s;
`;


const Content = styled(KeypadVerticalNavList)`
  display: flex-column;
  position: absolute;
  text-align: left;
  width:${vw(580)};
  height:90%;
  margin-left:${vw(45)};
  margin-top:${vw(5)};

 
`;
  const Header = styled.div`
    width:94%;
   h1 {
    margin-top: ${vw(499)};
    margin-right: auto;
    color: ${({ theme }) => theme.color.hotspotTab};
    font-family: ${({ theme }) => theme.font.tradegothic};
    font-weight: 700;
    font-style: normal;
    font-size: ${vw(64)};
    line-height: ${vw(64)};
  }

  `;
  const Body = styled.div`
  width:94%;
      p {
    margin-top: ${vw(38)};
    margin-right: auto;
    width: ${props=>props.width ? props.width : vw(519)};
    font-size: ${vw(22)};
    font-family: ${({ theme }) => theme.font.tradegothic};
    font-weight: 300;
    font-style: normal;
    line-height: ${vw(30)};
    color: ${({ theme }) => theme.color.hotspotTab};
  }
  `;

const Image = styled.img`
  position:absolute;
  top: ${vw(90)};
  max-width:${vw(548)};
  max-height:${vw(400)};
  box-sizing: border-box;
  border: ${vw(2)} solid ${({ theme }) => theme.color.tertiary};
`;

const CLoseOpenButton = styled(DivWithRefControl)`
  display: ${props=>props.show ? 'display' : 'none'};
  position: absolute;
  align-items: center;
  bottom: ${vw(347)};
  left: ${props=>props.active ? 95.5 : 100}%;
  width: ${vw(62)};
  height: ${vw(95)};
  background: ${({ theme }) => theme.color.secondary};
  transition: all 0.3s;


`;

const Arrow = styled.img`
  margin-left: auto;
  margin-right: auto;
`;

const ButtonSection = styled.div`
  position: absolute;
  top: ${vw(885)};
  width: ${vw(282)};
  height: ${vw(75)};
  background: ${({ theme }) => theme.color.primary};

  h3 {
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    padding-top: 5px;
    height: ${vw(50)};
    width: ${vw(80)};
    color: ${({ theme }) => theme.color.secondary};
    font-family: ${({ theme }) => theme.font.tradegothic};
    font-weight: 500;
    font-style: normal;
    font-size: ${vw(26)};
    line-height: ${vw(33)};
    white-space: nowrap;
  }
`;

const NextPrevButton = styled.button`
  position: absolute;
  width: ${vw(67)};
  height: ${vw(75)};
  top: ${vw(885)};
  left: ${(props) => vw(props.left)};
  background-image: url(${(props) => props.src});
  background-repeat: no-repeat;
  background-position: center;
  border-left: 1px solid white;
  border-right: ${(props) => (props.left === 0 ? ' 1px solid white' : ' 1px solid white')};
`;

const AudioElement = styled.div`
position: absolute;
  span{
      opacity: 0;
  }
`;