import React, { useRef, useEffect } from 'react';
import styled, { keyframes } from 'styled-components';
import PropTypes from 'prop-types';
import contentMatrix from '@/data/contentMatrix';
import { vw } from '@/styles/utils';
import Tooltip from './Tooltip';
import { loader } from "@/plugins/components/Preloader";
import { HOTSPOT_BUTTON_PLUS, HOTSPOT_BUTTON_MINUS } from '@/config/config';


export default function Hotspot({
  tabIndex,
  tabToggle,
  active,
  index,
  hotspotNums, 
  style,
  onClick,
  hotspot,
  activeHotspotIndex,
  show,
  hotspotFocusedIndex,
  handleKeyDownForHotspotFocus,
  tooltipRef,
  areHotspotsVisible,
  
}) {
  



  const handleClickHotspot = () => {
    onClick();
  };



  return (
    <Content 
      activeHotspotIndex={activeHotspotIndex} 
      areHotspotsVisible={areHotspotsVisible} 
    >
      <Root style={{ transform: `${style} ` }}>
    
        <Tooltip
          active={active}
          index={index}
          width={hotspot.width}
          show={show}
          forwardRef={tooltipRef}
          hotspot={hotspot}
          tabIndex={tabIndex}
          handleClick={handleClickHotspot}
          hotspotFocusedIndex={hotspotFocusedIndex}
          handlekeyDown={handleKeyDownForHotspotFocus}
          aria-label={`${index+1} of ${hotspotNums}, ${hotspot.title}, ${active ? 'selected' : ''}`}
          aria-roledescription={' '}
          >
          <h2>{hotspot.title}</h2>
        </Tooltip>

        <Button
          active={active}
          width={hotspot.width}
          src={active ? loader.get(HOTSPOT_BUTTON_MINUS).src : loader.get(HOTSPOT_BUTTON_PLUS).src}
        ></Button>
      </Root>
    </Content>
  );
}

Hotspot.propTypes = {
  onClick: PropTypes.func,
  style: PropTypes.string,
  show: PropTypes.bool,
  active: PropTypes.bool,
  tabIndex: PropTypes.number,
  activeHotspotIndex: PropTypes.number,
  hotspot: PropTypes.object,
};

Hotspot.defaultProps = {
  onClick: null,
  style: '',
  show: false,
  active: false,
  tabIndex: 1,
  activeHotspotIndex: null,
  hotspot: {},
};

const Content = styled.div`
  opacity: ${(props) => (props.areHotspotsVisible ? 1 : 0)};
  transition: opacity 0.2s ease-in-out;
`;

const Root = styled.div`
  border-radius: 50%;
  position: absolute;
  top: 0;
  z-index: ${(props) => (props.active ? 1000 : 0)};
  transform: translate(-50%, -50%);
`;


const Button = styled.div`
  position: relative;
  width: ${vw(48)};
  height: ${vw(48)};
  margin-left: ${props=>props.width>499 ? vw(-34) : vw(-10)};
  display: block;
  background-position: center;
  background-size: 50%;
  background-repeat: no-repeat;
  z-index: 3000;
  outline: none;
  pointer-events: none;
  /* transform: ${(props) => (props.active ? 'scale(0.2)' : 'scale(1)')}; */
  transform: scale(1);
  transition: all 0.5s;
  /* background: ${({ theme }) => theme.color.primary}; */
  /* border-radius: 50%; */
  background-image: url(${props=>props.src});
  background-size: contain;

`;
