import Speech from 'speak-tts';

import config from '@/config/config';

const { isVoiceOverWebSpeech } = config;

export const speech = new Speech();

speech.init({
  voice: 'Samantha',
});

export const speak = target => {
  const ariaLabel = target.getAttribute('aria-label') || '';

  let text = '';

  switch (target.tagName) {
    case 'IMG':
      text = `${ariaLabel ? ariaLabel : target.alt}. image.`;
      break;
    case 'H1':
    case 'H2':
    case 'H3':
    case 'H4':
    case 'H5':
    case 'H6':
      const headerLevel = target.tagName.substr(1, 1);
      text = `Header level ${headerLevel}. ${
        ariaLabel ? ariaLabel : target.outerText
      }.`;
      break;
    case 'BUTTON':
      text = `${ariaLabel ? ariaLabel : target.outerText}. Button.`;
      break;
    default:
      text = `${ariaLabel ? ariaLabel : target.outerText}.`;
      break;
  }

  speech.speak({ text });
};

export const read = el => {
  if (!isVoiceOverWebSpeech) return;
  let children = Array.from(el.children);

  if (children.length) {
    children.forEach(child => {
      speak(child);
    });
    return;
  }

  speak(el);
};

export const handleOnFocus = ({ target }) => {
  if (!isVoiceOverWebSpeech) return;
  read(target);
};

export const handleBlur = () => {
  if (!isVoiceOverWebSpeech) return;
  speech.cancel();
};

export const screenReaderHandlers = {
  onFocus: handleOnFocus,
  onBlur: handleBlur,
};
