import React from 'react';
import styled from 'styled-components';
import { vw } from '@/styles/utils';
import {useSelector } from 'react-redux';
import contentMatrix from '@/data/contentMatrix';
import { Slide } from '@/components/Animation';
import { loader } from "@/plugins/components/Preloader";
import { THREE_SIXTY_ICON, ARROW_CONTROL_ARROW_ACTIVE, ARROW_CONTROL_ARROW_PASSIVE } from '@/config/config';
import { isTimeoutPopupActiveSelector } from '@/redux/app';


export default function ArrowControls({
  active,
  handleArrows,
  handleFocus,
  arrowsContainerRef,
  arrowPressedIndex,
  isArrowControlActive,
  tabIndex,
})
{

  const isTimeoutPopupActive = useSelector(isTimeoutPopupActiveSelector);

  const onArrowClick =(index)=>{
    handleArrows(index);
  }
  return (
    <Slide direction="up" value={100}>
    <Root 
      active={active }
      ref={arrowsContainerRef}
      onFocus={handleFocus}
      tabIndex={tabIndex}
      aria-label={isTimeoutPopupActive ? '' : ((arrowPressedIndex!==null && arrowPressedIndex!==5) ?
         `${arrowPressedIndex+1} of ${contentMatrix.main.ArrowGroup.length}, ${contentMatrix.main.ArrowGroup[arrowPressedIndex].ad} button ` 
         :(!isArrowControlActive ? contentMatrix.main.tabGroup.arrowControlGroupADNotActive : arrowPressedIndex===null ? contentMatrix.main.tabGroup.arrowControlGroupADActive : ''))}
      aria-roledescription={' '} 
    >

        <ThreeSixtyIcon src={loader.get(THREE_SIXTY_ICON).src} />
        
        {contentMatrix.main.ArrowGroup.map((arrow, index) => (
            <Arrow
              key={index}
              src={arrowPressedIndex===index ? loader.get(ARROW_CONTROL_ARROW_PASSIVE).src : loader.get(ARROW_CONTROL_ARROW_ACTIVE).src}
              style={{ top: vw(arrow.top), left: vw(arrow.left) }}
              rotate={arrow.rotate}
              onClick={()=>onArrowClick(arrow.onClickIndex)}
              tabIndex={-1}
              active={arrowPressedIndex===arrow.onClickIndex}
            />
      ))}
    
    </Root>
    </Slide>
  );
}


const Root = styled.div`
  position: absolute;
  width: ${vw(204)};
  height: ${vw(198)};
  top: 79%;
  left: ${vw(1336)};

  opacity: ${(props) => (props.active ? 0 : 1)};
  transition: all 0.33s;
`;


const ThreeSixtyIcon = styled.img`
  position: absolute;
  width: ${vw(79)};
  height: ${vw(79)};
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
`;

const Arrow = styled.button`
  position: absolute;
  width: ${vw(51)};
  height: ${vw(51)};
  transform: rotate(${(props) => props.rotate}deg);
  background-image: url(${props=>props.src});
  background-size: contain;
  background-repeat: no-repeat;

  :active{
    background-image: url(${loader.get(ARROW_CONTROL_ARROW_PASSIVE).src});
  }
`;