import React from 'react';
import styled from 'styled-components';
import PropTypes from 'prop-types';

import { vw } from '@/styles/utils';
import { loader } from "@/plugins/components/Preloader";
import { HOTSPOT_BG_ACTIVE, HOTSPOT_BG_NOT_ACTIVE, HOTSPOT_BG_LONGER_NOT_ACTIVE, HOTSPOT_BG_LONGER_ACTIVE} from '@/config/config';

export default function Tooltip(props) {

  return (
    <Root
      {...props}
      ref={props.forwardRef}
      index={props.index}
      width={props.width}
      active={props.active}
      tabIndex={props.tabIndex}
      onClick={props.handleClick}
      onKeyDown={props.handlekeyDown}
    ></Root>
  );
}

Tooltip.propTypes = {
  active: PropTypes.bool,
  show: PropTypes.bool,
  forwardRef: PropTypes.object,
  hotspot: PropTypes.object,
};

Tooltip.defaultProps = {
  active: false,
  show: false,
  forwardRef: {},
  hotspot: {},
};

const Root = styled.div`

  display: flex;
    justify-content: center;
    align-items: center;
    width: ${props=>props.width ? vw(props.width) : vw(320)};
    height: ${vw(74)};
    position: absolute;
    top: 50%;
    left: 0;
    transform: translate(-11%, -50%) ;
    transition: all 0.25s;
    opacity: ${(props) => (props.show ? 1 : 0)}; 
    background-image: url(${(props) =>props.active ? loader.get(props.width ? HOTSPOT_BG_LONGER_ACTIVE : HOTSPOT_BG_ACTIVE).src : ( props.show ? loader.get(props.width ? HOTSPOT_BG_LONGER_NOT_ACTIVE : HOTSPOT_BG_NOT_ACTIVE).src : null)});
    background-size: contain;
    background-position: left;
    background-repeat: no-repeat;

  
  h2 {
 
    /* padding-top: ${vw(37)}; */
    /* background: green; */
    margin-left: ${props=>props.width>480? vw(57) : vw(50)};
    color: ${(props) =>
      props.active ? 'black' : ({ theme }) => theme.color.tertiary};
    font-family: ${({ theme }) => theme.font.tradegothic};
    font-weight: 500;
    font-style: normal;
    font-size: ${vw(22)};
    line-height: ${vw(7)};
    letter-spacing: ${vw(0.4)};
  }
`;
