import React, {useEffect, useState} from 'react';
import styled from 'styled-components';
import { vw } from '@/styles/utils';
import contentMatrix from '@/data/contentMatrix';
import PropTypes from 'prop-types';
import { Slide } from '@/components/Animation';
import { loader } from "@/plugins/components/Preloader";
import { SIDEBAR_PLANE_IMAGE, SIDEBAR_PLANE_DOT } from '@/config/config';
import KeypadVerticalNavList from '@/plugins/components/KeypadVerticalNavList';

export default function Sidebar({
  views,
  activeViewIndex,
  onClickTab,
  handleArrowBoxBlur,
  isActiveHotspot,
  tabIndex,
  arrowsContainerRef,
  startOverRef,
  sideTabRef,

}) {

  return (
    <Slide direction="left" value={100}>
    <Root isActiveHotspot={isActiveHotspot}>

      
      <h1 
      >
        {contentMatrix.main.sidebarTitle}
      </h1>
      <h2>you are here</h2>


      <Plane
        src={loader.get(SIDEBAR_PLANE_IMAGE).src}
        alt={`${contentMatrix.main.planeImageAlt1}, ${views[activeViewIndex].title}, ${contentMatrix.main.planeImageAlt2}`}
        // tabIndex={isActiveHotspot ? 4 : -1}
        tabIndex={-1}
      />
        <PlaneDot
        src={loader.get(SIDEBAR_PLANE_DOT).src}
        activeViewIndex={activeViewIndex}
      >
        <span>You are here</span>
      </PlaneDot>

      <Tabs 
        tabIndex={tabIndex}
        ref={sideTabRef}
        prevTabRef={arrowsContainerRef}
        nextTabRef={startOverRef} 
        onFocus={handleArrowBoxBlur}
        aria-label={' '}
        aria-roledescription={`${contentMatrix.main.tabGroup.sidebarGroupAD}`}
      > 
        
        {views.map((view, index) => (
          <Tab
            key={index}
            tabIndex={-1}
            index={index}
            aria-label ={`${index+1} of ${views.length}, ${view.title}, ${activeViewIndex === index ? 'selected' : ''}`}
            aria-roledescription={' '}
            active={activeViewIndex === index}
            onClick={() => onClickTab(index)}
          >
            <span>{view.title}</span>
          </Tab>
        ))}
        
      </Tabs>
     
    </Root>
    </Slide>
  );
}

Sidebar.propTypes = {
  views: PropTypes.array,
  activeViewIndex: PropTypes.number,
  onClickTab: PropTypes.func,
  isActiveHotspot: PropTypes.bool,
  indexTab: PropTypes.number,
};

Sidebar.defaultProps = {
  views: [],
  activeViewIndex: null,
  onClickTab: null,
  isActiveHotspot: false,
  indexTab: 1,
};

const Root = styled.div`
  position: absolute;
  display: flex;
  flex-direction: column;
  align-items: center;
  right: 0;
  width: 17.7%;
  height: 100%;
  background: ${({ theme }) => theme.color.primary};
  padding: ${vw(35)};

  h2 {
    position: absolute;
    top: ${vw(-100)};
    font-size: ${vw(22)};
    font-family: ${({ theme }) => theme.font.tradegothic};
    font-weight: 600;
    line-height: ${vw(25)};
    text-transform: uppercase;
    color: ${({ theme }) => theme.color.primary};
    text-transform: capitalize;
  }
  h1 {
    position: absolute;
    top: ${vw(22)};
    right: ${vw(50)};
    height: ${vw(220.5)};
    width: ${vw(242)};
    border-bottom: 3px solid #ffffff;
    font-size: ${vw(33)};
    line-height: ${vw(36)};
    color: ${({ theme }) => theme.color.secondary};
    font-family: ${({ theme }) => theme.font.tradegothic};
    font-weight: 700;
    font-style: normal;
    text-transform: uppercase;
    padding-top:${vw(50)};
    padding-left: ${vw(23)};
    span {
      font-size: ${vw(111)};
      line-height: ${vw(110)};
      margin-left: ${vw(-2)};

    }
  }
`;

const Tabs = styled(KeypadVerticalNavList)`
  position: absolute;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: ${vw(630)};
  bottom: ${vw(20)};
  width: ${vw(300)};
  height: ${vw(1050)};
`;

const Tab = styled.button`
  position: relative;
  width: ${vw(284)};
  height: ${vw(89)};
  display: block;
  text-align: center;
  border: 3px solid #fffffe;
  border-radius: ${vw(12)};
  background-color: ${(props) =>props.active ? '#E7DFC7' : props.theme.color.primary};
  margin-bottom: ${vw(50)};
  font-size: ${vw(22)};
  line-height: 1.27;
  
  span {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    padding-top: ${vw(5)};
    font-family: ${({ theme }) => theme.font.tradegothic};
    font-weight: 700;
    font-style: normal;
    font-size: ${vw(32)};
    line-height: ${vw(34)};
    letter-spacing: ${vw(0.91)};
    color: ${(props) => (props.active ? props.theme.color.primary : '#FFFFFF')};
    text-transform: capitalize;
    white-space: nowrap;
    :active{
    color:  ${({ theme }) => theme.color.primary};

  }
  }
  :active{
    background: #FFFFFF;

  }
`;
const Plane = styled.img`
  position: absolute;
  height: ${vw(284)};
  width: ${vw(284)};
  top: ${vw(289)};
  right: auto;
`;

const PlaneDot = styled.div`
  position: absolute;
  top: ${vw(289)};
  right: auto;
  height: ${vw(110)};
  width: ${vw(284)};
  background-image: url(${(props) => props.src});
  background-size: ${vw(44)} ${vw(44)};
  background-repeat: no-repeat;
  background-position: 50% ${(props) => props.activeViewIndex * 40 + 10}%;
  transition: all 0.1s;
 

  span {
    position: absolute;
    right: 5%;
    top: 15%;
    font-family: ${({ theme }) => theme.font.tradegothic};
    font-weight: 200;
    font-style: normal;
    font-size: ${vw(22)};
    line-height: ${vw(25)};
    color: #EBE8E1;
  }
`;
