import React, { useState, useEffect } from 'react';
import styled from 'styled-components';
import { vw } from '@/styles/utils';
import { Slide, Stagger } from '@/components/Animation';
import contentMatrix from '@/data/contentMatrix';
import { loader } from '@/plugins/components/Preloader';
import { ATTRACT_HAND } from '@/config/config';

export default function Content({ handleClick }) {
  const [isAnim, setIsAnim] = useState(true);
  useEffect(() => {
    const timeout = setTimeout(
      () => {
        setIsAnim(!isAnim);
      },
      isAnim ? 14000 : 1000
    );
    return () => {
      clearTimeout(timeout);
    };
  }, [isAnim]);

  return (
    <Stagger
      delay={1000}
      animate={isAnim}
      animationComponent={Slide}
      content={[
        <HeadingBox>
        <h1 >{contentMatrix.attract.title}</h1>
        </HeadingBox>,
        <Deco src={require('@/assets/images/attract/deco.svg')} />,

        <CTA onClick={handleClick} tabIndex={-1}>
          {contentMatrix.attract.buttonLabel}
          <CTAImage src={loader.get(ATTRACT_HAND).src} />
        </CTA>,
      ]}
    />
  );
}


const HeadingBox = styled.div`
  position: absolute;
  top: ${vw(0)};
  left: ${vw(0)};
  display: flex;
  justify-content: center;
  align-items: center;
  box-sizing: border-box;
  height: ${vw(282)};
  width: 100%;

  h1 {
    position: absolute;
    top: 40%;
    left: 4.5%;
    transform: translate(-0%, 0%);
    font-size: ${vw(96)};
    line-height: ${vw(102)};
    text-transform: uppercase;
    white-space: nowrap;
    letter-spacing: ${vw(0)};
    color: ${({ theme }) => theme.color.secondary};
    font-family: ${({ theme }) => theme.font.tradegothic};
    font-weight: 700;
    font-style: normal;
  }
`;

const Deco = styled.img`
  position: absolute;
  top: 0;
  left: 0;
  height: ${vw(44)};
  width: ${vw(560)};
`;


const CTA = styled.button`
  position: absolute;
  height: ${vw(64)};
  width: ${vw(239)};
  bottom: ${vw(104)};
  left: ${vw(821)};
  display: inline-block;
  padding-left: ${vw(65)};
  font-size: ${vw(22)};
  letter-spacing: ${vw(1)};
  line-height: ${vw(28)};
  font-family: ${({ theme }) => theme.font.tradegothic};
  font-weight: 500;
  font-style: normal;
  color: ${({ theme }) => theme.color.tertiary};

`;

const CTAImage = styled.img`
  position: absolute;
  top: 0%;
  left: 0%;
  width: ${vw(45)};
  height: ${vw(64)};
  animation: pulse 4s infinite;
  @keyframes pulse {
    0% {
      transform: scale(0.8);
    }

    70% {
      transform: scale(1);
    }

    100% {
      transform: scale(0.8);
    }
  }
`;

