import React, { useState } from 'react';
import { useHistory } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import styled from 'styled-components';
import StartOverButton from '@/components/StartOverButton';
import { vw } from '@/styles/utils';
import Page from '@/components/Page';
import AudioDescription from '@/components/AudioDescription';
import { setActiveView } from '@/redux/views';
import contentMatrix from '@/data/contentMatrix';
import { isTimeoutPopupActiveSelector } from '@/redux/app';
import { tabable } from '@/utils/screenreader';
import { loader } from '@/plugins/components/Preloader';
import { ATTRACT_DECO } from '@/config/config';

const views = contentMatrix.main.views;
export default function Main() {
  const history = useHistory();
  const dispatch = useDispatch();
  const isTimeoutPopupActive = useSelector(isTimeoutPopupActiveSelector);
  const activeViewIndex = useSelector((state) => state.views.activeViewIndex);

  const handlekeyDown = (e) => {
    if (activeViewIndex !== null) {
      if (e.key === 'Enter') {
        handleClick(activeViewIndex);
      }
    }
  };
  const handleOnFocusOnView = (index) => {
    dispatch(setActiveView(index));
  };

  const [clicked, setClicked] = useState(false);

  const handleClick = (index) => {
    if (!clicked) {
      setClicked(true);
      dispatch(setActiveView(index));
      history.push('/view');
    }
  };
  return (
    <Root>
      <BodyAD
        tabIndex={tabable(2, !isTimeoutPopupActive)}
        aria-label={contentMatrix.main.bodyAD}
        aria-roledescription={' '}
      />
      <h1>{contentMatrix.main.title}</h1>
      <h2>{contentMatrix.main.bodyText}</h2>
      <List>
        {views.map((view, index) => (
          <ButtonDiv
            tabIndex={tabable(3, !isTimeoutPopupActive)}
            onClick={() => handleClick(index)}
            onKeyDown={handlekeyDown}
            onFocus={() => handleOnFocusOnView(index)}
            aria-label={`${index + 1} of ${views.length}, ${view.title}, ${
              view.thumbAD
            } ${contentMatrix.main.selectToLearn}`}
            aria-roledescription={' '}
            key={index}
          >
            <Button
              data-testid={`button-${index}`}
              index={index}
              view={view}
              tabIndex="-1"
            ></Button>
          </ButtonDiv>
        ))}
      </List>
      <Deco src={loader.get(ATTRACT_DECO).src} />

      <AudioDescription
        description={contentMatrix.main.audioDescription}
        autoFocus={true}
        zindex={'999'}
      />
      <StartOverButton tabIndex={tabable(8, !isTimeoutPopupActive)} />
    </Root>
  );
}

const Root = styled(Page)`
  display: flex;
  align-items: center;
  justify-content: center;
  background: #09615e;

  h2 {
    position: absolute;
    top: ${vw(224)};
    left: ${vw(86)};
    width: ${vw(1200)};
    height: ${vw(92)};
    text-align: left;
    font-size: ${vw(26)};
    line-height: ${vw(46)};
    color: ${({ theme }) => theme.color.secondary};
    font-family: ${({ theme }) => theme.font.tradegothic};
    font-weight: 400;
    font-style: normal;
    span {
      margin-left: ${vw(-3)};
      font-size: ${vw(28)};
      line-height: ${vw(50)};
      color: #fff;
      font-family: ${({ theme }) => theme.font.tradegothic};
      font-weight: 500;
      font-style: normal;
    }
  }

  h1 {
    position: absolute;
    top: ${vw(111)};
    left: ${vw(86)};
    font-size: ${vw(96)};
    font-family: ${({ theme }) => theme.font.tradegothic};
    font-weight: 700;
    font-style: normal;
    line-height: ${vw(102)};
    text-transform: uppercase;
    letter-spacing: ${vw(0)};
    color: ${({ theme }) => theme.color.secondary};
  }
`;

